import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  Box,
  Typography,
  TextField,
  Button,
  Paper,
  InputAdornment,
  IconButton,
  CircularProgress
} from '@mui/material';
import { useFormik, FormikHelpers } from 'formik';
import * as Yup from 'yup';
import { toast } from 'react-toastify';
import PhoneAuth from '../components/PhoneAuth';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import api from '../utils/axiosConfig';
import type { ApiResponse } from './interfaces';
import { useTranslation } from 'react-i18next';

interface LoginFormValues {
  password: string;
}

const Login: React.FC = () => {
  const navigate = useNavigate();
  const { t } = useTranslation();
  const [phoneNumber, setPhoneNumber] = useState('');           // +91XXXXXXXXXX
  const [isRegistered, setIsRegistered] = useState<boolean | null>(null);
  const [loadingPhoneCheck, setLoadingPhoneCheck] = useState(false);
  const [showPassword, setShowPassword] = useState(false);

  /**
   * Check if phone exists on server
   * @param phone - Phone number in E.164 format (e.g. +919876543210)
   */
  const checkUserExists = async (phone: string) => {
    // Prevent duplicate requests for the same number
    if (loadingPhoneCheck) {
      console.log('Check already in progress, ignoring duplicate request');
      return;
    }
    
    console.log('Checking if user exists:', phone);
    setLoadingPhoneCheck(true);
    
    try {
      const res = await api.post<ApiResponse>('/users/check-phone', { phone });
      const exists = Boolean(res.data.exists);
      console.log('User exists check result:', exists);
      setIsRegistered(exists);
      
      // Log for analytics and debugging
      if (exists) {
        console.log('Phone verified as registered user');
      } else {
        console.log('Phone verified as new user');
      }
    } catch (error: any) {
      console.error('Error checking user existence:', error);
      setIsRegistered(null);
      toast.error(error.message || t('login.error'));
    } finally {
      setLoadingPhoneCheck(false);
    }
  };

  // Trigger phone existence check when phoneNumber changes (debounced, only for valid numbers)
  useEffect(() => {
    let timeout: NodeJS.Timeout;
    if (/^\+91\d{10}$/.test(phoneNumber)) {
      timeout = setTimeout(() => {
        checkUserExists(phoneNumber);
      }, 350); // 350ms debounce
    } else {
      setIsRegistered(null);
    }
    return () => clearTimeout(timeout);
  }, [phoneNumber, t]);

  // Formik for password login
  const formik = useFormik<LoginFormValues>({
    initialValues: { password: '' },
    validationSchema: Yup.object({
      password: Yup.string()
        .required(t('login.validation.passwordRequired'))
        .min(6, t('login.validation.passwordMinLength'))
    }),
    onSubmit: async (values, helpers: FormikHelpers<LoginFormValues>) => {
      console.log('Attempting login with phone:', phoneNumber);
      helpers.setSubmitting(true);
      
      try {
        const res = await api.post<ApiResponse>('/users/login', {
          phone: phoneNumber,
          password: values.password
        });
        
        console.log('Login response received:', { success: !!res.data.token });

        if (res.data.token) {
          // Save authentication token
          localStorage.setItem('token', res.data.token);
          api.defaults.headers.common['Authorization'] = `Bearer ${res.data.token}`;
          
          // Clear any previous errors
          helpers.setErrors({});
          
          toast.success(t('login.success'));
          console.log('User successfully authenticated, redirecting to profile');
          
          // Brief delay to allow toast to show before navigation
          setTimeout(() => {
            navigate('/profile', { replace: true });
          }, 300);
        } else {
          console.warn('Login response missing token');
          toast.error(t('login.error'));
          helpers.setFieldError('password', t('login.invalidCredentials', 'Invalid credentials'));
        }
      } catch (err: any) {
        console.error('Login error:', err);
        
        // Map server-side validation errors
        if (err.details) {
          console.log('Validation errors from server:', err.details);
          Object.entries(err.details).forEach(([field, detail]: any) => {
            helpers.setFieldError(field, detail.message || t('login.error'));
          });
        } else if (err.response?.status === 401) {
          // Handle specific status codes
          helpers.setFieldError('password', t('login.invalidCredentials', 'Invalid credentials'));
          toast.error(t('login.invalidCredentials', 'Invalid credentials'));
        } else {
          // Generic error handling
          toast.error(err.message || t('login.error'));
        }
      } finally {
        helpers.setSubmitting(false);
      }
    }
  });

  return (
    <Container maxWidth="sm" sx={{ py: 8 }}>
      <Paper elevation={3} sx={{ p: { xs: 3, sm: 5 }, borderRadius: 2 }}>
        <Box display="flex" flexDirection="column" alignItems="center">
          <Typography variant="h4" fontWeight={600} mb={4}>
            {t('login.title')}
          </Typography>

          {/* Phone input field */}
          <TextField
            fullWidth
            label={t('login.phone')}
            value={phoneNumber.replace(/^\+91/, '')}
            onChange={e => {
              // Only allow 10 digits after +91
              const digits = e.target.value.replace(/\D/g, '').slice(0, 10);
              setPhoneNumber('+91' + digits);
            }}
            InputProps={{
              startAdornment: <InputAdornment position="start">+91</InputAdornment>
            }}
            error={!!phoneNumber && !/^\+91\d{10}$/.test(phoneNumber)}
            helperText={!!phoneNumber && !/^\+91\d{10}$/.test(phoneNumber)
              ? t('login.invalidPhone')
              : ''
            }
            sx={{ mb: 2 }}
          />

          {/* Status indicator for phone verification */}
          {phoneNumber && (
            <Box sx={{ mt: 1, mb: 2, display: 'flex', alignItems: 'center', gap: 1 }}>
              {loadingPhoneCheck ? (
                <>
                  <CircularProgress size={20} />
                  <Typography variant="body2" color="text.secondary">
                    {t('login.checkingPhone', 'Verifying phone number...')}
                  </Typography>
                </>
              ) : isRegistered === true ? (
                <Typography variant="body2" color="success.main">
                  ✓ {t('login.registeredUser', 'Registered user detected')}
                </Typography>
              ) : isRegistered === false ? (
                <Typography variant="body2" color="primary">
                  {t('login.newUser', 'New user detected, please verify with OTP')}
                </Typography>
              ) : null}
            </Box>
          )}

          {/* If not registered, show OTP flow (only for valid phone numbers) */}
          {isRegistered === false && /^\+91\d{10}$/.test(phoneNumber) && (
            <PhoneAuth
              phoneNumber={phoneNumber}
              onVerify={verified => navigate('/register', { state: { fromLogin: true, phone: phoneNumber, fromOTP: true } })}
            />
          )}

          {/* If registered, show password login (only for valid phone numbers) */}
          {isRegistered === true && /^\+91\d{10}$/.test(phoneNumber) && (
            <Box component="form" onSubmit={formik.handleSubmit} width="100%">
              <TextField
                fullWidth
                name="password"
                label={t('login.password')}
                type={showPassword ? 'text' : 'password'}
                value={formik.values.password}
                onChange={formik.handleChange}
                error={formik.touched.password && Boolean(formik.errors.password)}
                helperText={formik.touched.password && formik.errors.password}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton onClick={() => setShowPassword(!showPassword)}>
                        {showPassword ? <VisibilityOff /> : <Visibility />}
                      </IconButton>
                    </InputAdornment>
                  )
                }}
                sx={{ mb: 3 }}
              />

              <Button
                type="submit"
                fullWidth
                variant="contained"
                disabled={formik.isSubmitting}
              >
                {formik.isSubmitting ? <CircularProgress size={20} /> : t('login.submit')}
              </Button>
            </Box>
          )}
        </Box>
      </Paper>
    </Container>
  );
};

export default Login;
